import React from 'react';
import styled from 'styled-components';
import { ReactSVG } from 'react-svg';

function Link({ src, className, iconClass, iconSrc, label, children, ...props }) {
	const icon = <ReactSVG
		afterInjection={(error, svg) => {
			if (error) {
				console.error(error)
				return
			}
			console.log(svg)
		}}
		beforeInjection={(svg) => {
			svg.classList.add('link__icon-svg')
		}}
		className={"link__icon " + iconClass}
		fallback={() => <span>Error!</span>
		}
		loading={() => <span>Loading</span>}
		src={iconSrc}
		wrapper="span"
	/>
	const setIcon = iconSrc ? icon : '';

	return (
		<a
			href={src}
			className={className}
			target="_blank"
			rel="noreferrer"
			{...props}
		>
			{setIcon}
			{label}
			{children}
		</a>
	);
};

export default styled(Link)`
	display: inline-flex;
	justify-content: space-between;
	line-height: 1.5;
	min-width: 25%;
	margin-bottom: 1.5rem;
	padding: .5rem 1rem .5rem .5rem;
	border-radius: 18px;
  border: 1px solid #E5E5E5;
  background: white;
  color: #404040;
  transition: all 0.25s;
  outline: none;
	font-size: 2rem;
	text-decoration: none;
	text-shadow: 0px 1px 1px rgba(0,0,0,0.4);
	text-align: right;
	letter-spacing: 2px;

	&:last-child {
		margin-bottom: 0;
	}

	.link__icon-svg {
		width: 50px;
		margin-right: 1rem;
		animation-duration: 1s;
		animation-fill-mode: both;
		animation-timing-function: ease-in-out;
	}

  &:hover {
    cursor: pointer;
    background: #EDEDED;
  }

	&:hover .link__icon-svg {
    animation-name: bounce;
  }

  &:focus, &:active {
    background: #DBDBDB;
    border: 1px solid #7F7F7F;
  }

	.link__icon {
		fill-opacity: 1;
	}

	.twitch {
		margin-bottom: -13px;
	}

	.youtube {
		position: relative;
		top: 2px;
		margin-bottom: -13px;
	}
`
